.page {
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    background-color: #e5e5e5;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
      z-index: 2;
    }

    @include desktop {
      display: none;
    }
  }

  &__section {
    margin-bottom: 120px;

    @include tablet {
      margin-bottom: 148px;
    }
  }
}
